<script>
import {mapActions, mapState} from "vuex";

export default {
  props: ['idQuiz'],
  data() {
    return {
      puntaja: 0,
      answersSection6: [
        ["a)","Permite a las personas inventar o crear cosas de manera única y especial.",false],
        ["b)","Permite tener todo ordenado.",false],
        ["c)","Permite organizarse y ser empático con los demás.",false]
      ],
      answersSection7:[
        ["a)",4,false],
        ["b)",5,false],
        ["c)",6,false]
      ],
      answersSection8:[
        ["a)","El ADN",false],
        ["b)","El DNI",false],
        ["c)","La ONU",false]
      ],
      additional: [],
      //TODO: mostrar borde rojo al usuario si no llena los inputs
      answer1: "",
      source: "",
      file: "",
      photoPlaceholder: require("@/assets/images/activities/upload.jpg")
    };
  },
  created() {
    this.getQuizesById(this.idQuiz);
    console.log('aaaaaaaaaaa')
    this.quizes.questions.forEach(
        item =>{
          this.puntaja=this.puntaja + item.points
    })
    console.log(this.puntaja, 'Puntaje')

  },
  methods: {
    ...mapActions([
      "getQuizesById",
    ]),
    advanced_success_alert: function () {
      this.$swal({
        text: '¿Estás seguro de que quieres emprezar a realizar el cuestionario?',
        showCancelButton: true,
        confirmButtonText: '¡Si !',
        confirmButtonColor: '#4466f2',
        cancelButtonText: 'Cancelar',
        cancelButtonColor: '#4466f2',
      }).then((result) => {
        if (result.value) {
          this.$router.push({name:'quiz',params:{id: this.idQuiz}})
        } else {
          /*this.$swal({
            text:"Pikachu fainted! You gained 500 XP!"
          });*/
        }
      });
    },
    check_one(){
      this.additional = [];
    },
    setOptions (e,i) {
      const value = e.target.value;
      let a =""
      if(i==6){
        a = this.answersSection6
      }else if(i==7){
        a = this.answersSection7
      }else{
        a = this.answersSection8
      }
      const rpta = a.map((k)=>{
        if(k[1]==value){
          k[2]=true
        }else{
          if(k[2] == true){
            k[2] = false
          }
        }
        return k
      })
      if(i==6){
        this.answersSection6 = rpta
      }else if(i==7){
        this.answersSection7 = rpta
      }else{
        this.answersSection8 = rpta
      }
    },
    onFileChange(e) {
      const file = e.target.files[0];
      this.file = file;
      const reader = new FileReader();
      reader.onload = e => {
        this.source = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    submit() {
      const allAnswers = {
        files: [{ order: 1, file: this.file }],
        answers: [{ order: 0, answer: this.answer1 }]
      };
      console.log(allAnswers);
      this.$emit("allAnswers", allAnswers);
    }
  },
  computed: {
    ...mapState([
      "quizes",
    ])
  }
};
</script>

